import emailjs from 'emailjs-com';
import { FormEvent } from 'react';

const sendEmail = (e: FormEvent): Promise<boolean> => {
    const form = e.target as HTMLFormElement;
    return emailjs.sendForm('gmail', 'template_tua14sj', form, 'user_968wDcgPKo0Pn55BwZxDz')
        .then((result) => {
            return true
        }, (error) => {
            return false
        });
}

export default sendEmail;